import * as events from 'events';
import Axios from 'axios';
import { parse } from 'path';
// //PeerConnection连接
// var RTCPeerConnection;
// //会话描述
// var RTCSessionDescription;
// //连接配置
var configuration;
// 创建一个空数组来存储RTCIceCandidate对象  
let iceCandidatesArray = [];
/**
 * 信令类
 */
export default class P2PVideoCall extends events.EventEmitter {
    //构造函数
    constructor(p2pUrl, turnUrl, name, roomId) {
        super();
        //Socket
        this.socket = null;
        //所有PeerConnection
        this.peerConnections = {};
        //会话Id
        this.sessionId = '000-111';
        //自己Id
        this.userId = 0;
        //用户名
        this.name = name;
        //房间号
        this.roomId = roomId;
        //信令服务器url
        this.p2pUrl = p2pUrl;
        //中转服务器url
        this.turnUrl = turnUrl;
        //本地媒体流
        this.localStream;

        // //RTCPeerConnection兼容性处理
        // RTCPeerConnection = window.RTCPeerConnection || window.mozRTCPeerConnection || window.webkitRTCPeerConnection || window.msRTCPeerConnection;
        // //RTCSessionDescription兼容性处理
        // RTCSessionDescription = window.RTCSessionDescription || window.mozRTCSessionDescription || window.webkitRTCSessionDescription || window.msRTCSessionDescription;
        // //getUserMedia兼容性处理
        // navigator.getUserMedia = navigator.getUserMedia || navigator.mozGetUserMedia || navigator.webkitGetUserMedia || navigator.msGetUserMedia;

        //ICE配置
        //configuration = { "iceServers": [{ "url": "stun:stun.l.google.com:19302" }] };
        //configuration = { "iceServers": [{ "urls": "stun:stun.gmx.net" }]};
        //configuration = { "iceServers": [{ "urls": "stun:stun.voipbuster.com" }] };
        //configuration = { "iceServers": [{ "urls": "stun:stun.gmx.net" }, { "urls": "stun:stun.voipbuster.com" }] };
        // configuration = {
        //     "iceServers": [
        //         { "urls": "stun:stun.gmx.net" },
        //         { "urls": "stun:stun.voipbuster.com" },
        //         { "urls": "stun:stun.voipstunt.com" },
        //         { "urls": "stun:stun.internetcalls.com" },
        //         { "urls": "stun:stun.voip.aebc.com" },]
        // };
        //STUN服务器
        const stunServers = { urls: "stun:stun.voipbuster.com" };
        configuration = { iceServers: [stunServers] };
        //访问Turn中转服务器
        Axios.get(this.turnUrl, {}).then(res => {
            //如果服务器正常返回
            if (res.status === 200) {
                let _turnCredential = res.data;
                const turnServer = {
                    urls: _turnCredential['uris'][0],
                    username: _turnCredential['username'],
                    credential: _turnCredential['password']
                };
                //如果访问成功，把TurnServer也添加到ICEServers里
                configuration = { iceServers: [stunServers, turnServer] };
                //console.log("TurnServer服务器:" + JSON.stringify(turnServer));
            }
        }).catch((error) => {
            console.log('网络错误:请求不到TurnServer服务器');
        });

        //初始化WebSocket
        this.socket = new WebSocket(this.p2pUrl);
        //连接打开
        this.socket.onopen = () => {
            console.log("WebSocket连接成功...");
            //获取随机数作为Id
            this.userId = this.getRandomUserId();
            //定义消息
            let message = {
                //加入房间
                'type': 'joinRoom',
                //数据
                'data': {
                    //用户名
                    name: this.name,
                    //用户Id
                    id: this.userId,
                    //房间Id
                    roomId: this.roomId,
                }
            }
            //发送消息
            this.send(message);
        };

        //Socket消息处理
        this.socket.onmessage = (e) => {
            //解析JSON消息
            var parsedMessage = JSON.parse(e.data);
            console.info('收到的消息: {\n type = ' + parsedMessage.type + ', \n data = ' + JSON.stringify(parsedMessage.data) + '\n}');
            //判断条件为消息类型
            switch (parsedMessage.type) {
                case 'offer':
                    this.onOffer(parsedMessage);
                    break;
                case 'answer':
                    this.onAnswer(parsedMessage);
                    break;
                case 'candidate':
                    this.onCandidate(parsedMessage);
                    break;
                case 'updateUserList':
                    this.onUpdateUserList(parsedMessage);
                    break;
                case 'leaveRoom':
                    this.onLeaveRoom(parsedMessage);
                    break;
                case 'quitRoom':
                    this.onQuitRoom(parsedMessage)
                    break;
                case 'hangUp':
                    this.onHangUp(parsedMessage);
                    break;
                case 'heartPackage':
                    console.log('服务端发心跳包!');
                    break;
                default:
                    console.error('未知消息', parsedMessage);
            }
        };
        //Socket连接错误
        this.socket.onerror = (e) => {
            console.log('onerror::' + e);
        }
        //Socket连接关闭
        this.socket.onclose = (e) => {
            console.log('onclose::' + e);
        }
    }

    //获取本地媒体流
    getLocalStream = (type) => {
        return new Promise((pResolve, pReject) => {
            //设置约束条件
            var constraints = { audio: true, video: (type === 'video') ? { width: 1920, height: 1080 } : false };
            //屏幕类型
            if (type == 'screen') {
                //调用getDisplayMedia接口获取桌面流
                navigator.mediaDevices.getDisplayMedia({ video: true }).then((mediaStream) => {
                    pResolve(mediaStream);
                }).catch((err) => {
                    console.log(err.name + ": " + err.message);
                    pReject(err);
                }
                );
            } else {
                //调用getUserMedia接口获取音视频流
                navigator.mediaDevices.getUserMedia(constraints).then((mediaStream) => {
                    pResolve(mediaStream);
                }).catch((err) => {
                    console.log(err.name + ": " + err.message);
                    pReject(err);
                }
                );
            }
        });
    }

    //获取6位随机id
    getRandomUserId() {
        var num = "";
        for (var i = 0; i < 6; i++) {
            num += Math.floor(Math.random() * 10);
        }
        return num;
    }

    //发送消息,将消息转成JSON串后发送
    send = (data) => {
        this.socket.send(JSON.stringify(data));
    }

    /**
     * 发起会话
     * remoteUserId为被呼叫的Id
     * media为会话类型 音频|视频|共享桌面
     */
    startCall = (remoteUserId, media) => {
        //本地Id+远端Id组成
        this.sessionId = this.userId + '-' + remoteUserId;
        //获取本地媒体流
        this.getLocalStream(media).then((stream) => {
            //取得到本地媒体流
            this.localStream = stream;
            //提议方创建连接PeerConnection
            this.createPeerConnection(remoteUserId, media, true, stream);
            //派发本地流事件
            this.emit('localstream', stream);
            //派发新的呼叫事件
            this.emit('newCall', this.userId, this.sessionId);
        });
    }

    //挂断处理
    hangUp = () => {
        //定义消息
        let message = {
            //消息类型
            type: 'hangUp',
            //数据
            data: {
                //当前会话Id
                sessionId: this.sessionId,
                //消息发送者
                from: this.userId,
                //房间Id
                roomId: this.roomId,
            }
        }
        //发送消息
        this.send(message);
    }

    //退出房间处理
    quitRoom = () => {
        //定义消息
        let message = {
            //消息类型
            type: 'quitRoom',
            //数据
            data: {
                //消息发送者
                from: this.userId,
                //房间Id
                roomId: this.roomId
            }
        }
        //发送消息
        this.send(message)
    }

    /**
     * 创建提议Offer
     * pc:PeerConnection对象
     * id:对方Id
     * media:媒体类型
     */
    createOffer = (pc, id, media) => {
        //创建提议
        pc.createOffer((desc) => {
            //console.log('createOffer: ', desc.sdp);
            console.log('pc createOffer', desc);
            //设置本地描述
            pc.setLocalDescription(desc, () => {
                console.log('pc setLocalDescription', pc.localDescription);
                //消息
                let message = {
                    //消息类型为offer
                    type: 'offer',
                    //数据
                    data: {
                        //对方Id
                        to: id,
                        //本地Id
                        from: this.userId,
                        //SDP信息
                        description: { 'sdp': desc.sdp, 'type': desc.type },
                        //会话Id
                        sessionId: this.sessionId,
                        //媒体类型
                        media: media,
                        //房间Id
                        roomId: this.roomId,
                    }
                }
                //发送消息
                this.send(message);
            }, this.logError);
        }, this.logError);
    }

    /**
     * 创建PeerConnection
     * id:对方Id
     * media:媒体类型
     * isOffer:true是提议方|false是应答方
     * localstream:本地媒体流
     */
    createPeerConnection = (id, media, isOffer, localstream) => {
        console.log("创建PeerConnection");
        //创建连接对象
        console.log("###############################");
        console.log(configuration)
        console.log("###############################");
        var pc = new RTCPeerConnection(configuration);
        //将PC对象放入集合里
        this.peerConnections["" + id] = pc;
        console.log('put pc to peerConnections')
        //收集到Candidate数据
        pc.onicecandidate = (event) => {
            console.log('pc onicecandidate', event);
            if (event.candidate) {
                //消息
                let message = {
                    //Candidate消息类型
                    type: 'candidate',
                    //数据
                    data: {
                        //对方Id
                        to: id,
                        //自己Id
                        from: this.userId,
                        //Candidate数据
                        candidate: {
                            'sdpMLineIndex': event.candidate.sdpMLineIndex,
                            'sdpMid': event.candidate.sdpMid,
                            'candidate': event.candidate.candidate,
                        },
                        //会话Id
                        sessionId: this.sessionId,
                        //房间Id
                        roomId: this.roomId,
                    }
                }
                //发送消息
                this.send(message);
            }
        };

        pc.onnegotiationneeded = () => {
            console.log('pc onnegotiationneeded');
        }

        pc.oniceconnectionstatechange = (event) => {
            console.log('pc oniceconnectionstatechange', event);
        };
        pc.onsignalingstatechange = (event) => {
            console.log('pc onsignalingstatechange', event);
        };
        //远端流到达
        pc.onaddstream = (event) => {
            console.log('***********************************************************');
            console.log('pc onaddstream', event, event.stream);
            //通知应用层处理流
            this.emit('addstream', event.stream);
            console.log('############################################################');
        };
        //远端流移除
        pc.onremovestream = (event) => {
            console.log('pc onremovestream', event, event.stream);
            //通知应用层移除流
            this.emit('removestream', event.stream);
        };

        //添加本地流至PC里
        pc.addStream(localstream);
        console.log('pc addlocalstream', localstream)
        //如果是提议方创建Offer
        if (isOffer) {
            this.createOffer(pc, id, media);
        }
        return pc;
    }

    //更新用户列表
    onUpdateUserList = (message) => {
        var data = message.data;
        console.log("users = " + JSON.stringify(data));
        //通知应用层渲染成员列表
        this.emit('updateUserList', data, this.userId);
    }

    //提议方发过来的Offer处理
    onOffer = (message) => {
        //获取数据
        var data = message.data;
        //读取发送方Id
        var from = data.from;
        console.log("data:" + data);
        //响应这一端默认用视频
        var media = 'video';//data.media;
        //读取会话Id
        this.sessionId = data.sessionId;
        //通知应用层新的呼叫
        this.emit('newCall', from, this.sessionId);

        //应答方获取本地媒体流
        this.getLocalStream(media)
            .then((stream) => {
                //获取到本地媒体流
                this.localStream = stream;
                //通知应用层本地媒体流
                this.emit('localstream', stream);
                //应答方创建连接PeerConnection
                var pc = this.createPeerConnection(from, media, false, stream);

                if (pc && data.description) {
                    console.log('pc setRemoteDescription', pc.remoteDescription);
                    //应答方法设置远端会话描述SDP
                    pc.setRemoteDescription(new RTCSessionDescription(data.description))
                        .then(() => {
                            // 应答方设置远端描述成功后，添加之前收到的 ICE 候选者  
                            while (iceCandidatesArray.length > 0) {
                                const candidate = iceCandidatesArray.shift();
                                pc.addIceCandidate(candidate)
                                    .catch((error) => {
                                        console.error('Failed to add ICE candidate:', error);
                                    });
                                console.log('pc addIceCandidate', candidate);
                            }
                            // 如果远端描述是 offer，则创建并发送 answer  
                            if (pc.remoteDescription.type === "offer") {
                                return pc.createAnswer();
                            }
                        })
                        .then((desc) => {
                            if (!desc) {
                                return; // 如果远端描述不是 offer，则直接返回  
                            }
                            console.log('pc createAnswer:', desc);
                            // 设置本地会话描述 SDP  
                            return pc.setLocalDescription(desc);
                        })
                        .then(() => {
                            if (!pc.localDescription || pc.localDescription.type !== "answer") {
                                return; // 如果没有本地描述或类型不是 answer，则直接返回  
                            }
                            console.log('pc setLocalDescription', pc.localDescription);
                            // 构造并发送 answer 消息  
                            const message = {
                                //应答消息类型
                                type: 'answer',
                                //数据
                                data: {
                                    //对方Id
                                    to: from,
                                    //自己Id
                                    from: this.userId,
                                    //SDP信息
                                    description: { sdp: pc.localDescription.sdp, type: pc.localDescription.type },
                                    //会话ID
                                    sessionId: this.sessionId,
                                    //房间Id
                                    roomId: this.roomId,
                                }
                            };
                            // 发送消息  
                            this.send(message);
                        })
                        .catch((error) => {
                            // 任何步骤出错都会进入这里  
                            this.logError;
                        });
                }
            })
            .catch((error) => {
                this.logError;
            });
    }

    //应答方发过来的Answer处理
    onAnswer = (message) => {
        //提取数据
        var data = message.data;
        //对方Id
        var from = data.from;
        var pc = null;
        //迭代所有的PC对象
        if (from in this.peerConnections) {
            //根据Id找到提议方的PC对象
            pc = this.peerConnections[from];
        }
        if (pc && data.description) {
            //发起方收到answer设置完远端sdp，RTC握手就算完成了
            console.log('pc setRemoteDescription', pc.remoteDescription);
            //提议方设置远端描述信息SDP
            pc.setRemoteDescription(new RTCSessionDescription(data.description))
                .then(() => { })
                .catch((error) => {
                    console.error('logError:', error);
                });
        }
    }

    //接收到对方发过来的Candidate信息
    onCandidate = (message) => {
        var data = message.data;
        var from = data.from;
        var pc = null;
        console.log(from)
        //将应答端提前收到的ICE信息缓存到数组里
        let candidate = new RTCIceCandidate(data.candidate);
        iceCandidatesArray.push(candidate);
        //根据对方Id找到PC对象
        if (from in this.peerConnections) {
            pc = this.peerConnections[from];
        }
        else {
            console.log(`未找到 ${from} 的peerconnect,还没有被创建出来,先缓存起来`, candidate)
        }
        //添加Candidate到PC对象中
        if (pc && data.candidate) {
            while (iceCandidatesArray.length > 0) {
                let candidate = iceCandidatesArray.shift()
                pc.addIceCandidate(candidate);
                console.log('pc addIceCandidate', candidate);
            }
        }
    }

    //对方意外中断P2P连接处理
    onLeaveRoom = (message) => {
        var id = message.data;
        console.log('leave', id);
        var peerConnections = this.peerConnections;
        var pc = peerConnections[id];
        if (pc !== undefined) {
            pc.close();
            delete peerConnections[id];
            this.emit('leave', id);
        }
        if (this.localStream != null) {
            this.closeMediaStream(this.localStream);
            this.localStream = null;
        }
        this.emit('leaveRoom')
    }

    //自己退出房间处理
    onQuitRoom = (message) => {
        //通知上层媒体层更新界面
        this.emit('quitRoom')
    }

    //挂断处理
    onHangUp = (message) => {
        var data = message.data;
        //sessionId是由自己和远端Id组成,使用下划线连接
        var ids = data.sessionId.split('-');
        var to = data.to;
        console.log('挂断:sessionId:', data.sessionId);
        //取到两个PC对象
        var peerConnections = this.peerConnections;
        var pc1 = peerConnections[ids[0]];
        var pc2 = peerConnections[ids[1]];
        //关闭pc1
        if (pc1 !== undefined) {
            console.log("关闭视频");
            pc1.close();
            delete peerConnections[ids[0]];
            console.log('删除连接:', ids[0])
        }
        //关闭pc2
        if (pc2 !== undefined) {
            console.log("关闭视频");
            pc2.close();
            delete peerConnections[ids[1]];
            console.log('删除连接：', ids[1])
        }
        //关闭媒体流
        if (this.localStream != null) {
            this.closeMediaStream(this.localStream);
            this.localStream = null;
        }
        //发送结束会话至上层应用
        this.emit('hangUp', to, this.sessionId);
        //将会话Id设置为初始值000-111
        this.sessionId = '000-111';
    }

    logError = (error) => {
        console.log("logError", error);
    }

    //关闭媒体流
    closeMediaStream = (stream) => {
        if (!stream)
            return;
        //获取所有的轨道
        let tracks = stream.getTracks();
        //循环迭代所有轨道并停止
        for (let i = 0, len = tracks.length; i < len; i++) {
            tracks[i].stop();
        }
    }


    //析构p2pVideoCall实例
    dispose() {
        if (this.socket) {
            //正常关闭websocket
            this.socket.close(1000, "quitRoom");
            this.socket = null;
        }
        //关闭媒体流
        if (this.localStream) {
            //获取所有的轨道
            let tracks = this.localStream.getTracks();
            //循环迭代所有轨道并停止
            for (let i = 0, len = tracks.length; i < len; i++) {
                tracks[i].stop();
            }
            this.localStream = null;
        }
        console.log('P2PVideoCall disposed');
    }
}